import Choices from '../../node_modules/choices.js/public/assets/scripts/choices.min.js';


// Dropdown menu ========================================================================================================================================================

document.addEventListener("DOMContentLoaded", () => {

  const menuButtons = document.querySelectorAll('[data-js-dropdown-button]');
  const dropDownMenus = document.querySelectorAll('[data-js-dropdown-menu]')
  const overlay = document.querySelector('.overlay');

  menuButtons.forEach(btn  => {
    btn.addEventListener('click', (e) => {
      let currentButton = e.currentTarget;
      let currentDropdown = currentButton.closest('.menu__item').querySelector('[data-js-dropdown-menu]');

      menuButtons.forEach(btn => {
        if (btn !== currentButton) {
          btn.classList.remove('menu__btn-active');
          overlay.classList.remove('open');
        }
      })

      dropDownMenus.forEach(drop => {
        if (drop !== currentDropdown) {
          drop.classList.remove('dropdown-active');
          overlay.classList.remove('open');
        }
      })

      btn.classList.toggle('menu__btn-active');
      currentDropdown.classList.toggle('dropdown-active');
      overlay.classList.toggle('open');
    })
  })

  document.addEventListener('click', (e) => {
    if (!e.target.closest('.menu')) {

      menuButtons.forEach(btn => {
        btn.classList.remove('menu__btn-active');
        overlay.classList.remove('open');
      })

      dropDownMenus.forEach(drop => {
        drop.classList.remove('dropdown-active');
        overlay.classList.remove('open');
      })

    }
  })
})

