// Count UP

if (document.querySelectorAll('.numbers').length > 0) {
  let countUpArr = new Array();
  function countUpFunction(id, finalNum, option = {}) {
    let mergeOptions = {
      duration: 4,
      scrollSpyOnce: true,
      ...{
        separator: ' ',
        enableScrollSpy: true,
      },
      ...option
    };
    countUpArr.push(new CountUp(id, finalNum, mergeOptions));
  }

  countUpFunction('count-up-ware', 10043);
  countUpFunction('count-up-production', 432100);
  countUpFunction('count-up-donat', 173213);
  countUpFunction('count-up-thread', 713);
  countUpFunction('count-up-parcel', 90000);
  countUpFunction('count-up-work', 8760);
}

