// Scrolling header ========================================================================================================================================================

if (document.querySelectorAll('.header').length > 0) {

  function scrollingHeader() {

    const header = document.querySelector('.header');

    if (document.documentElement.scrollTop > 1 || document.body.scrollTop > 1) {
      header.classList.add('scrolling');
    } else {
      header.classList.remove('scrolling');
    }
  }

  window.addEventListener('scroll', () => {
    scrollingHeader();
  });

  document.addEventListener('DOMContentLoaded', () => {
    scrollingHeader();
  })
}


if (document.querySelectorAll('.checkout').length > 0) {

  // Tabs

  const tabButtons = document.querySelectorAll(".tab__links");

    for (let i = 0; i < tabButtons.length; i++) {
      tabButtons[i].addEventListener("click", function () {
        let tabContent = document.querySelector('#' + tabButtons[i].dataset.tab)
        let allTabContent = document.querySelectorAll(".tab__content");
        let allTabButtons = document.querySelectorAll(".tab__links");

        for (let j = 0; j < allTabContent.length; j++) {
          allTabContent[j].style.display = "none";
        }

        for (let k = 0; k < allTabButtons.length; k++) {
          allTabButtons[k].classList.remove("active");
        }

        tabContent.style.display = "block";
        this.classList.add("active");
      });
    }

    document.querySelector(".tab__links").click();

  // Choice js

  const selectDeliveryCity = document.querySelector('#deliveryCity');
  const selectDeliveryBranch = document.querySelector('#deliveryBranch');

    const choicesDeliveryCity = new Choices(selectDeliveryCity, {
      searchEnabled: true,
      itemSelectText: '',
  });

    const choicesDeliveryBranch = new Choices(selectDeliveryBranch, {
      placeholder: true,
      searchEnabled: false,
      itemSelectText: '',
      shouldSort: false,
  });


  // Stepper

  const steppers = document.querySelectorAll('.stepper');

  steppers.forEach(steppersEl => {
    const stepperInput = steppersEl.querySelector('.stepper__input');
    const stepperBtnUp = steppersEl.querySelector('.stepper__btn-up');
    const stepperBtnDown = steppersEl.querySelector('.stepper__btn-down');

    let count = stepperInput.value;

    const isNotApple = () => {
      if (!/iPhone|iPad|iPod/i.test(navigator.userAgent)) {
        return false;
      }
      return true;
    };

    function allowNumbersOnly(e) {
      var code = (e.which) ? e.which : e.keyCode;
      if (code > 31 && (code < 48 || code > 57)) {
          e.preventDefault();
      }
    }

    stepperInput.addEventListener('keyup', (e) => {
      let self = e.currentTarget;

      if (self.value == '0') {
        self.value = 1;
      }

      if (isNotApple) {
        self.style.width = `${self.value.length + 1}ex`;
      } else {
        self.style.width = `${self.value.length + 2}ex`;
      }

      count = stepperInput.value;

      if (count == 1) {
        stepperBtnDown.classList.add('stepper__btn--disabled');
      } else {
        stepperBtnDown.classList.remove('stepper__btn--disabled');
      }
    });

    stepperInput.addEventListener('keypress', (e) => {
      allowNumbersOnly(e);
    });

    stepperInput.addEventListener('change', (e) => {
      let self = e.currentTarget;

      if (!self.value) {
        self.value = 1;
      }

      count = stepperInput.value;

      if (count == 1) {
        stepperBtnDown.classList.add('stepper__btn--disabled');
      } else {
        stepperBtnDown.classList.remove('stepper__btn--disabled');
      }
    });

    stepperBtnUp.addEventListener('click', (e) => {
      e.preventDefault();

      count++;

      if (count == 1) {
        stepperBtnDown.classList.add('stepper__btn--disabled');
      } else {
        stepperBtnDown.classList.remove('stepper__btn--disabled');
      }

      stepperInput.value = count;

      if (isNotApple) {
        stepperInput.style.width = `${stepperInput.value.length + 1}ex`;
      } else {
        stepperInput.style.width = `${stepperInput.value.length + 2}ex`;
      }
    });

    stepperBtnDown.addEventListener('click', (e) => {
      e.preventDefault();

      count--;

      if (count == 1) {
        stepperBtnDown.classList.add('stepper__btn--disabled');
      } else {
        stepperBtnDown.classList.remove('stepper__btn--disabled');
      }

      stepperInput.value = count;

      if (isNotApple) {
        stepperInput.style.width = `${stepperInput.value.length + 1}ex`;
      } else {
        stepperInput.style.width = `${stepperInput.value.length + 2}ex`;
      }
    });
  })

}

